* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

@media screen and (min-width: 1280px) {
  .view-range {
    max-width: 1280px;
    margin-left: auto;
    margin-right: auto;
  }
}

.content {
  padding-left: 16px;
  padding-right: 16px;
}

@media screen and (min-width: 780px) {
  .content {
    padding-left: 24px;
    padding-right: 24px;
  }
}

@media screen and (min-width: 1280px) {
  .content {
    padding-left: 100px;
    padding-right: 100px;
  }
}

.colors {
  background-color: blue;
  color: white;
  border-color: blue;
  color: #f00;
}

.slogan-contact {
  background-color: #000;
  color: red;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-wrap: wrap;
  height: 60px;
  background-color: #000;
}

@media screen and (min-width: 780px) {
  .slogan-contact {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: auto;
  }
}

.slogan {
  flex-basis: 100%;
  text-align: center;
  margin-bottom: 8px;
}

@media screen and (min-width: 780px) {
  .slogan {
    flex-basis: auto;
    margin-bottom: 0;
  }
}

.slogan__text {
  color: #fff;
}

.contact {
  display: flex;
  color: #fff;
}

.contact__icon {
  margin-left: 4px;
  margin-right: 4px;
}

.contact__num-tel {
  margin-left: 4px;
}

