  color: #f00;
}

.banner {
  color: red;
}

.banner-pic {
  display: block;
  width: 100%;
  height: 300px;
}

@media screen and (min-width: 780px) {
  .banner-pic {
    height: 200px;
  }
}

@media screen and (min-width: 1280px) {
  .banner-pic {
    height: 160px;
  }
}

.banner-info {
  color: red;
}

.banner-info__company-name {
  color: red;
}

.banner-info__company-info {
